/* ProductCard.module.css */
.productCard {
    /* Ajusta el tamao general de la tarjeta */
    /* Puedes cambiar estos valores segn tus necesidades */
    width: 100%;
    max-width: 500px; /* Cambia el ancho mximo de la tarjeta */
  
    /* Agrega ms estilos segn sea necesario */
  }
  
  .cardMedia {
    /* Ajusta el tamao de la imagen */
    /* Puedes cambiar estos valores segn tus necesidades */
    max-width: 100%; /* Ajusta el ancho mximo de la imagen */
  }
  
  .cardImage {
    /* Ajusta el tamao de la imagen */
    /* Puedes cambiar estos valores segn tus necesidades */
    width: 100%; /* Ajusta el ancho de la imagen */
    height: auto; /* Mantiene la proporcin de la imagen */
  }
  